@mixin arrow($direction:'right',$color:#666,$border:1px,$size:7px) {
    content: '';
    display: inline-block;
    position: relative;
    //
    border-top: $border solid $color;
    border-left: $border solid $color;
    width: $size;
    height: $size;
    margin: auto; 
    //
    @if $direction == up {
        transform: rotate(45deg);
        top: 0px;
        bottom:0;
    } @else if $direction == right {
        transform: rotate(135deg) skew(0deg);
        top: 0px;
        bottom:0;
        left: 3px;
        // top: 1px;
        margin: auto 0;
        transition:left .15s ease-in-out;
    } @else if $direction == down {
        transform: rotate(225deg);
        top: -4px;
        left: 3px;
    } @else if $direction == left {
        top: 0px;
        bottom:0;
        left:3px;
        transform: rotate(-45deg);
    }
}
