@import "../bootstrap5/functions";
@import "../bootstrap5/variables"; 
@import "../bootstrap5/mixins";
@import "../mixins";
@import "reboot";

//message
.c-dashboard-message{
    .panel{
        position: relative;
        box-shadow: 0 4px 4px 0 rgba(94,109,23,0.14);
    }
    .label {
        padding:3px 15px;
        border-radius: 5px;
    }
    .label.is-orange, a.label:link.is-orange{
        background-color: #FCA832;
    }
    .message{
        // padding:8px 0 0;
        padding:0;
    }
    a.item{
        border:0;
        &:first-of-type{
            border:0;
        }
        @media (hover: hover) and (pointer: fine) {
            &:hover{
                color:#00A94F;
            }
        }
        &:active{
            color:#00A94F;
        }
        
        
        
    }

    /* one item */
    a.item:first-child:nth-last-child(1) {
        border:0;
        border-bottom: 1px solid #D7E4E4;
        @media (min-width:map-get($grid-breakpoints, 'lg')){
            padding:16px 24px;
        }
    }
    
    /* two items */
    a.item:first-child:nth-last-child(2),
    a.item:first-child:nth-last-child(2) ~ a.item {
        border:0;
        border-bottom: 1px solid #D7E4E4;

        &:first-of-type{
            border-top:0;
        }
        @media (min-width:map-get($grid-breakpoints, 'lg')){
            padding:16px 24px;
        }
    }
    
    /* three items */
    a.item:first-child:nth-last-child(3),
    a.item:first-child:nth-last-child(3) ~ a.item {
        border:0;
        border-bottom: 1px solid #D7E4E4;
        @media (min-width:map-get($grid-breakpoints, 'lg')){
            padding:10px 24px;
        }
    }
    .title{
    }
    .panel.member-panel .action {
        padding:14px 24px;
    }
    .public_at{
        font-size:14px;
        color:#5B616F;
        line-height: 1;
        
    }
}
.c-dashboard-news-more-btn{
    position: absolute;
    right: 22px;
    bottom: 10px;

    font-size:16px;
    font-weight: 400;
    color:#00A94F;
    line-height: 1;
    display: flex;
    align-items: center;
    &:after{
        @include arrow('right',#00A94F,2px,8px);
    }
    @media (hover: hover) and (pointer: fine) {
        &:hover{
            opacity:.8;
        }
    }
    &:active{
        opacity:.8;
    }

}
.c-dashboard-wrap{
    display:flex;
    margin-bottom: -4px;
}
.c-dashboard{
    display: block;
    width: 100%;

    @media (min-width:map-get($grid-breakpoints, 'lg')){
        max-width: 540px;
    }

    @media (min-width:map-get($grid-breakpoints, 'xl')){
        max-width: 680px;
    }
}

.c-dashboard-inner{
    margin-right: auto;
    margin-left: auto; 
    position: relative; 
    display:flex;
    flex-direction: column;
    border: 1px solid #D7E4E4;
    border-radius:10px;
    overflow:hidden;
    box-shadow: 0 4px 4px 0 rgba(94,109,23,0.14);
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        flex-direction: row;
        align-items: center;
        background-color: #fff;
    }
    // 
}
.c-dashboard-info{
    position: relative;
    background-color: #F0F5F5;
    flex-basis:100%;
    max-width: 100%;
    padding:20px 0 24px 20px;
    min-height:170px;

    @media (min-width:map-get($grid-breakpoints, 's')){
        padding:24px 24px 14px 24px;
    }
    @media (min-width:map-get($grid-breakpoints, 'md')){
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        flex-basis:280px;
        max-width: 280px;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        height:268px;
        padding:24px 14px 14px 14px;
    }
    @media (min-width:map-get($grid-breakpoints, 'xl')){
        padding:24px 24px 14px 24px;
        flex-basis:330px;
        max-width: 330px;
    }
}

.c-dashboard-func{
    flex-basis:100%;
    max-width: 100%;
    padding:28px 0 18px; //bottom padding -10px for func-item-margin-bottom

    
    background-color: #fff;
    @media (min-width:map-get($grid-breakpoints, 's')){
        padding:24px 0 14px;
    }
    @media (min-width:map-get($grid-breakpoints, 'md')){
        padding:24px 50px 14px;
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        padding:0px;
        flex:1;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
    }
    display:flex;
    flex-direction: row;
    justify-content:space-evenly;
    flex-wrap: wrap;
}
.c-dashboard-message{
    display: none;
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        display: block;
        // flex-basis:33%;
        // max-width: 33%;
        // background-color: rgba(200,200,200,.5);
        flex: 1;
    margin-left: 20px;
    }
}

//mascot
.c-dashboard-mascot{
    position: absolute;
    right:0;
    bottom:0;
    pointer-events: none;
    @media (min-width:map-get($grid-breakpoints, 'md')){
        right:72px;
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        right:0;
    }
}

//dialog
.c-dashboard-promotion-copy-label{
    font-size:16px;
    line-height: 1;
    color:#00A94F;
    margin-top: 5px;
    @media (min-width:map-get($grid-breakpoints, 's')){
        margin-top: 0;
        margin-left: 5px;
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        // margin-top: 5px;
        // margin-left: 0;
    }
    @media (min-width:map-get($grid-breakpoints, 'xl')){
        // margin-top: 0;
        // margin-left: 5px;
    }

}
.c-dashboard-copied-dialog{
    position: absolute;
    pointer-events: none;
    left: 20px;
    top: -42px;
    @media (min-width:map-get($grid-breakpoints, 's')){
        // left:auto;
        // right:-28px;
    }
    display: block;
    background-color: rgba(0,0,0,.75);
    border:0;
    line-height: 1;
    border-radius: 5px;
    opacity:0;
    transform:translateY(-5px);
    transition:transform .5s ease-in-out,opacity .5s ease-in-out;
    &.show{
        opacity:1;
        transform:translateY(0px);
    }
    &.leave{
        opacity:0;
    }
    b{
        line-height: 1;
        position: relative;
        color:#fff;
        font-weight: 300;
        letter-spacing: 1px;
        font-size:16px;
        display: block;
        padding:8px 8px;
    } 
    &:after{
        content:'';
        position: absolute;
        top:98%;
        right:0;
        left:0;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 5px 5px 0 5px;
        border-color: rgba(0,0,0,.75) transparent transparent transparent;
        margin-left:auto;
        margin-right:auto;
    }
}
//info

.c-dashboard-info-name{
    color: #000;
    font-size: 24px;
    line-height: 1.3;
    letter-spacing: 0;
    font-weight: 700;
    max-width: calc(100% - 50px);
    @media (min-width:map-get($grid-breakpoints, 'md')){
        max-width: calc(100% - 50px);
    }
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        font-size: 28px;
    }
}
.c-dashboard-info-func{
    display:flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
}

a.c-dashboard-member{
    &.c-dashboard-member--normal{
        font-size: 16px;
        font-weight: 400;
        line-height: 1;
        color: #5b616f;
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-right:14px;
        @media (min-width:map-get($grid-breakpoints, 's')){
            margin-right: 22px;
        }
        @media (min-width:map-get($grid-breakpoints, 'lg')){
            margin-right: 30px;
        }
        position: relative;
        &:after{
            @include arrow("right",#02aa50,2px,9px);
        }

        @media (hover: hover) and (pointer: fine) {

            &:hover{
                color:#38bb76;
                text-decoration: none;
    
                &:after{
                    border-color:#38bb76;
                }
            }
        }
        &:active{
            color:#38bb76;
            text-decoration: none;

            &:after{
                border-color:#38bb76;
            }
        }
    }
    &.c-dashboard-member--vip{
        font-size: 16px;
        font-weight: 400;
        line-height: 1;
        color: #5b616f;
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-right: 16px;
        @media (min-width:map-get($grid-breakpoints, 'lg')){
            margin-right: 24px;
        }
        position: relative;
        transition:opacity .3s;
        &:after{
            @include icon('info');
        }
        &:hover{
            opacity:.8;
            text-decoration: none;
            &:after{
                opacity:.8;
            }
        }
        @media (hover: hover) and (pointer: fine) {
            &:hover{
                opacity:.8;
                text-decoration: none;
                &:after{
                    opacity:.8;
                }
            }

        }
        &:active{
            opacity:.8;
            text-decoration: none;
            &:after{
                opacity:.8;
            }
        }
    }
} 
a.c-dashboard-line{
    &.c-dashboard-line--no{
        border-top-left-radius:25px;
        border-top-right-radius:25px;
        border-bottom-left-radius:25px;
        border-bottom-right-radius:25px;
        padding: 8px 18px 8px 16px;
        font-size:15px;
        @media (min-width:map-get($grid-breakpoints, 's')){
            font-size:16px;
            padding: 8px 20px 8px 14px;
        }
        @media (min-width:map-get($grid-breakpoints, 'lg')){
            padding: 8px 20px 8px 14px;
            
        }
        font-weight: 400;
        line-height: 1;
        color:#fff;
        background-color: #00B900;
        transition:background-color .5s;
        &:after{
            @include arrow("right",#fff,2px,9px);
            top: -1px;
            bottom:0;
            margin:auto 0;
            left:3px;
        }
        @media (hover: hover) and (pointer: fine) {

            &:hover{
                color:#fff;
                text-decoration: none;
                background-color: #31c531;
            }
        }
        &:active{
            color:#fff;
            text-decoration: none;
            background-color: #31c531;
        }
    }
    &.c-dashboard-line--ok{
        font-size: 16px;
        font-weight: 400;
        line-height: 1;
        color: #5b616f;
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-right: 30px;
        position: relative;
        cursor:default;
        pointer-events: none;
        &:before{
            display:none;
        }
        &:after{
            @include icon('check');
        }
        @media (hover: hover) and (pointer: fine) {
            &:hover{
                pointer-events: none;
                color:#5b616f;
                text-decoration: none;
                opacity:1;
                &:after{
                    border-color:#00B900;
                }
            }
        }
        &:active{
            pointer-events: none;
            color:#5b616f;
            text-decoration: none;
            opacity:1;
            &:after{
                border-color:#00B900;
            }
        }
        
    }
}
