.c-dashboard-info-func-share{
    flex-basis:100%;
    max-width: 100%;
    display:flex;
    flex-direction: row;
    align-items: center;
}
a.c-dashboard-info-func-item-link,.c-dashboard-info-func-item-link{
    font-size:16px;
    font-weight: 400;
    line-height: 1;
    color:#5B616F;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 30px;
    &:last-of-type{
        margin-right: 0;
    }

   
    @media (hover: hover) and (pointer: fine) {
        &:hover{
            color:#00A94F;
            i{
                opacity:.8;
            }
        }
    }
    &:active{
        color:#00A94F;
        i{
            opacity:.8;
        }
    }

    &.arrow{
        &:after{
            @include arrow('right',#00A94F,2px,8px);
            left:4px;
            top:1px;
            transition:left .2s;
        }
    }
    &.check{
        &:after{
            content:'';
            position: relative;
            display: inline-block;
            transform: rotate(45deg);
            height: 13px;
            width: 6px;
            top:0;
            right: -10px;
            border-bottom: 2px solid #00A94F;
            border-right: 2px solid #00A94F;
        }
    }
    i{
        margin-left: 6px;
        margin-top: 3px; //shift for notosans tc line-height
    }
    img{
        display: block;
    }
}
