///func
.c-dashboard-func-item{
    flex-basis:33%;
    max-width: 33%;
    margin-bottom: 10px;
    @media (min-width:map-get($grid-breakpoints, 'md')){
    }
    
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        margin-bottom: 40px;
        &:nth-last-child(-n+3){
            margin-bottom: 0;
        }
    }
}
.c-dashboard-func-item-link{
    display:flex;
    flex-direction: column;
    align-items: center;
    i{
        pointer-events: none;
    }
    u{
        display: block;
    }
    b{
        display: none;
    }

    @media (hover: hover) and (pointer: fine) {
        
        &:hover{
            u{
                display: none;
            }
            b{
                display: block;
            }
            .c-dashboard-func-item-title{
                color:#00A94F;
            }
        }
    }
    &:active{
        u{
            display: none;
        }
        b{
            display: block;
        }
        .c-dashboard-func-item-title{
            color:#00A94F;
        }
    }
}
.c-dashboard-func-item-img{
}
.c-dashboard-func-item-title{
    font-size:16px;
    color:#000;
    line-height: 1.5rem;
    font-weight:400;
}

//share
.c-dashboard-share{
    display:flex;
    align-items: center;
    position: relative;
}
.c-dashboard-share-item{
    margin-left: 7px;
    margin-right: 7px;
    position: relative;

    &:first-of-type{
        margin-left: 0;
    }
    &:last-of-type{
        margin-right: 0;
    }
    cursor:pointer;
    img{
        transform:scale(1);
        transition:transform .2s;
    }
    @media (hover: hover) and (pointer: fine) {
        
        &:hover{
            img{
                transform:scale(1.2);
            }
        }
    }
    &:active{
        img{
            transform:scale(1.2);
        }
    }
}
